:root {
  --main-bg-color: #F6C6EA;
  --main-active-color: #CDF0EA;
  --secondary-bg-color: #FAF4B7;
  --secondary-active-color: #F9F9F9;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  color: rgba(0, 0, 0, 0.8);
}

.App {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.Card {
  width: 100vw;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  text-align: center;
  background-image: url("https://picsum.photos/1920/1080");
  background-repeat: no-repeat;
  background-position: center;
  background-color: var(--secondary-bg-color);
}

.button {
  background-color:
    /*var(--main-active-color)*/
    #cdf0eaec;
  color: rgba(0, 0, 0, 0.8);
  text-decoration: none;
  width: 12rem;
  padding: .8rem 0;
  border-radius: 10px;
  margin-top: 1rem;
  text-align: center;
  transition: 500ms;
  font-weight: bold;
  box-shadow: 0px 2px rgba(0, 0, 0, 0.2);
}

.button:hover {
  background-color: var(--secondary-active-color);
}

.avatar img {
  border-radius: 100%;
  width: 150px;
  border: 5px solid var(--secondary-active-color);
  margin: 1.5rem 0;
}

h1 {
  font-size: 1.5rem;
  display: block;
}

p {
  margin: .5rem 0 1rem 0;
}

h1,
p {
  font-weight: lighter;
  color: white;
  text-shadow: 1px 1px 1px black;
}

/*---*/
/*---*/
/* FADE IN EFFECT ON PAGE LOAD */

.Card>* {
  -webkit-animation: fadein 2.5s;
  /* Safari, Chrome and Opera > 12.1 */
  -moz-animation: fadein 2.5s;
  /* Firefox < 16 */
  -ms-animation: fadein 2.5s;
  /* Internet Explorer */
  -o-animation: fadein 2.5s;
  /* Opera < 12.1 */
  animation: fadein 2.5s;
}

@keyframes fadein {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

/* Firefox < 16 */
@-moz-keyframes fadein {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

/* Safari, Chrome and Opera > 12.1 */
@-webkit-keyframes fadein {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

/* Internet Explorer */
@-ms-keyframes fadein {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

/* Opera < 12.1 */
@-o-keyframes fadein {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

/*---*/
/*---*/

@media only screen and (min-width: 425px) {
  #root {
    background-color: var(--secondary-active-color);
  }

  .Card {
    max-height: 720px;
    max-width: 425px;
    border-radius: 50px;
    box-shadow: 5px 5px var(--main-active-color);
  }
}



@media only screen and (min-width: 2150px) {

  .Card {
    max-height: 1000px;
    max-width: 625px;
  }

  h1 {
    font-size: 2rem;
  }

  p,
  .button {
    font-size: 1.5rem;
  }

  .button {
    width: 16rem;
    margin: 1rem;
  }

  .avatar img {
    width: 200px;
  }
}

/* Adapting to smallest screens*/
@media only screen and (max-height: 590px) {
  .App {
    height: 130vh;
  }
}